import {EventName} from "../eventname/EventName";
import {SVGNameSpace} from "../svgnamespace/SVGNameSpace";
import ParticleEmitter from "./ParticleEmitter";
import DeviceInfo from "../device/DeviceInfo";
/**
 * メインのレイヤー
 */
export default class ParticleLayer {
  public view:SVGGElement;

  private frame:number = 0;

  private _isMouseDown:boolean;   // マウスが押されているかどうか
  private _particleEmitter:ParticleEmitter;   // パーティクル発生装置のインスタンス

  private _tickCount:number = 0;
  private mouseX:number = 0;
  private mouseY:number = 0;

  public constructor(svgField:SVGSVGElement) {
    this.view = document.createElementNS(SVGNameSpace.SVG, "g");

    this._particleEmitter = new ParticleEmitter(svgField);  // パーティクル発生装置のインスタンスを作成
    this.view.appendChild(this._particleEmitter.view);

    if (!DeviceInfo.isTouchDevice) {
      window.addEventListener(EventName.MOUSE_DOWN, (event) => this.mouseDownHandler(event));
      window.addEventListener(EventName.MOUSE_MOVE, (event) => this.mouseMoveHandler(event));
      window.addEventListener(EventName.MOUSE_UP, (event) => this.mouseUpHandler(event));
    } else {
      window.addEventListener(EventName.TOUCH_START, (event) => this.mouseDownHandler(event));
      window.addEventListener(EventName.TOUCH_MOVE, (event:TouchEvent) => this.touchMoveHandler(event));
      window.addEventListener(EventName.TOUCH_END, (event) => this.mouseUpHandler(event));
    }

    this.tick();
  }

  private tick():void {
    this.frame++;

    requestAnimationFrame(() => this.tick())

    if (this.frame % 2 && DeviceInfo.lowSpecMode)
    {
      return;
    }

    this.update();
  }

  /*
   * マウスを押した時の処理
   * */
  private mouseDownHandler(event):void {
    this._isMouseDown = true;
  }

  /**
   * マウスを離した時の処理
   */
  private mouseUpHandler(event):void {
    this._isMouseDown = false;
  }

  /**
   * Tickイベントで実行される処理
   */
  private mouseMoveHandler(event:MouseEvent):void {
    // マウスの座標
    this.mouseX = event.clientX;
    this.mouseY = event.clientY;
    // DebugController.instance.trace(`this.mouseX : ${this.mouseX }`, `this.mouseY : ${this.mouseY}`);
  }

  /**
   * Tickイベントで実行される処理
   */
  private touchMoveHandler(event:TouchEvent):void {
    // マウスの座標
    this.mouseX = event.changedTouches[0].clientX;
    this.mouseY = event.changedTouches[0].clientY;
    // DebugController.instance.trace(`this.mouseX : ${this.mouseX }`, `this.mouseY : ${this.mouseY}`);
  }



  public update():void {
    // パーティクル発生装置の座標を更新
    this._particleEmitter.update(this.mouseX, this.mouseY);

    if (this._isMouseDown) {

      this._particleEmitter.emitParticle();
      this._tickCount++;

      if (this._tickCount >= 1000)
        this._tickCount = 0;
    }
  }
}



