/**
 * イベント名の名前空間
 */
export namespace EventName {
  export const RESIZE:"resize" = "resize";
  export const DOM_CONTENT_LOADED:"DOMContentLoaded" = "DOMContentLoaded";

  export const MOUSE_DOWN:"mousedown" = "mousedown";
  export const MOUSE_MOVE:"mousemove" = "mousemove";
  export const MOUSE_UP:"mouseup" = "mouseup";

  export const TOUCH_START:"touchstart" = "touchstart";
  export const TOUCH_MOVE:"touchmove" = "touchmove";
  export const TOUCH_END:"touchend" = "touchend";
}


